<template>
  <div class="load">
    <h1 class="title"></h1>
    <div class="from">
      <Form ref="loadForm" :model="loadData" :label-width="80">
        <FormItem
          label=""
          prop="user"
          :rules="{
            required: true,
            min: 1,
            message: '',
            trigger: 'blur'
          }"
        >
          <i-input type="text" v-model="loadData.user" placeholder=""></i-input>
        </FormItem>
        <FormItem
          label=""
          prop="password"
          :rules="{
            required: true,
            min: 6,
            message: '6',
            trigger: 'blur'
          }"
        >
          <i-input type="password" v-model="loadData.password" placeholder=""></i-input>
        </FormItem>
      </Form>
    </div>
    <div class="btn">
      <Button class="lod" type="primary" @click="loadOK"></Button>
      <Button></Button>
    </div>
  </div>
</template>

<script>
import api from "@/fetch/api";
import { mapActions } from "vuex";

export default {
  name: "Load",
  data() {
    return {
      loadData: {
        user: "",
        password: ""
      }
    };
  },
  methods: {
    ...mapActions(["changeLoad"]),
    loadOK() {
      if (this.loadData.user.length == 0 || this.loadData.password == 0) {
        this.$Message.error("");
        return;
      }
      api.load(this.loadData.user, this.loadData.password).then(data => {
        if (data.status === "ok") {
          this.changeLoad(true);
          this.$router.push("/home");
        } else {
          console.log("==> load info: ", data.msg, " status: ", data.status);
          this.$Message.warning({
            content: "",
            duration: 2
          });
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.load {
  height: 420px;
  width: 420px;
  margin: 100px auto;
  border: 2px solid #555;
  border-radius: 10px;
  text-align: center;
  .title {
    margin: 50px;
  }
  .btn {
    margin-top: 60px;
    .lod {
      margin-right: 80px;
    }
  }
  .from {
    width: 350px;
  }
}
</style>
