<template>
  <div class="account">
    <Table
      border
      :highlight-row="true"
      :columns="account_label"
      :data="getAccountsWithState($route.params.state)"
    >
      <template slot-scope="{ row }" slot="account_no">
        <strong>{{ row.account_no }}</strong>
      </template>
      <template slot-scope="{ row }" slot="action">
        <Button
          type="primary"
          size="small"
          style="margin-right: 5px"
          @click="edit(row)"
          ></Button
        >
        <Button type="error" size="small" @click="remove(row)"></Button>
      </template>
    </Table>
      <EditAccount
        :showModal.sync="showEditAccount"
        :accountDetail="activeRow"
      ></EditAccount>
  </div>
</template>

<script>
import EditAccount from "@/components/EditAccount";
import util from "@/util/util"
import { mapState, mapGetters, mapActions } from "vuex";

export default {
  name: "Account",
  components: {
    EditAccount
  },
  data() {
    return {
      showEditAccount: false,
      activeRow: {},
      account_label: [
        {
          title: "",
          slot: "account_no"
        },
        {
          title: "id",
          key: "id"
        },
        {
          title: "",
          key: "agent_name"
        },
        {
          title: "",
          key: "machine_no"
        },
        {
          title: "IP",
          key: "machine_ip"
        },
        {
          title: "",
          key: "machine_password"
        },
        {
          title: "",
          key: "create_date",
          sortable: true
        },
        {
          title: "",
          key: "agent_date",
          sortable: true
        },
        {
          title: "",
          key: "end_date",
          sortable: true
        },
        {
          title: "()",
          key: "time_left",
          sortable: true
        },
        {
          title: "Action",
          slot: "action",
          width: 150,
          align: "center"
        }
      ]
    };
  },
  computed: {
    ...mapState(["accounts"]),
    ...mapGetters(["getAccountsWithState"])
  },
  methods: {
    ...mapActions(["delAccount"]),
    edit(row) {
      this.showEditAccount = true;
      // 
      this.activeRow = util.deepCopy(row);
    },
    remove(item) {
      console.log("=====> remove item:", item);
      this.delAccount({ me: this, data: item });
    }
  }
};
</script>

<style lang="scss" scoped>
.account {
  height: 100%;
}
</style>
