<template>
  <div class="add-agent">
    <Modal
      v-model="show"
      title=""
      :loading="loading"
      @on-ok="asyncOK('agentFormValidate')"
    >
      <Form
        ref="agentFormValidate"
        :model="agentFormValidate"
        :label-width="80"
      >
        <FormItem
          label=""
          prop="name"
          :rules="{ required: true, message: '', trigger: 'blur' }"
        >
          <i-input
            v-model="agentFormValidate.name"
            placeholder=""
          ></i-input>
        </FormItem>
        <FormItem
          label=""
          prop="account"
          :rules="{
            required: true,
            message: '->xiaowang',
            trigger: 'blur'
          }"
        >
          <i-input
            v-model="agentFormValidate.account"
            placeholder=""
          ></i-input>
        </FormItem>
        <FormItem
          label=""
          prop="password"
          :rules="{
            required: true,
            min: 6,
            message: '6',
            trigger: 'blur'
          }"
        >
          <i-input
            v-model="agentFormValidate.password"
            placeholder=""
          ></i-input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "AddAgent",
  props: {
    showModal: Boolean
  },
  data() {
    return {
      show: false, // show 
      loading: true,
      agentFormValidate: {
        name: "", // 
        account: "", // 
        password: "" // 
      }
    };
  },
  methods: {
    ...mapActions(["addAgent"]),
    asyncOK(name) {
      this.$refs[name].validate(valid => {
        if (!valid) {
          this.$Message.warning("");
        } else {
          this.addAgent({
            me: this,
            data: {
              agent_name: this.agentFormValidate.name,
              agent_account: this.agentFormValidate.account,
              agent_password: this.agentFormValidate.password
            }
          });
        }
      });
      this.show = false;

      this.$refs[name].resetFields();
    }
  },
  watch: {
    showModal: function() {
      this.show = this.showModal;
    },
    show: function() {
      this.$emit("update:showModal", this.show);
    }
  }
};
</script>

<style lang="scss" scoped></style>
