<template>
  <div class="add-machine">
    <Modal
      v-model="show"
      title=""
      :loading="loading"
      @on-ok="asyncOK('machineFormValidate')"
    >
      <Form
        ref="machineFormValidate"
        :model="machineFormValidate"
        :label-width="120"
      >
        <FormItem
          label=""
          prop="machine_no"
          :rules="{
            required: true,
            message: ', a101',
            trigger: 'blur'
          }"
        >
          <i-input
            v-model="machineFormValidate.machine_no"
            placeholder=""
          ></i-input>
        </FormItem>
        <FormItem
          label="IP"
          prop="machine_ip"
          :rules="{
            required: true,
            message: 'IP192.168.1.1:6666',
            trigger: 'blur'
          }"
        >
          <i-input
            v-model="machineFormValidate.machine_ip"
            placeholder="IP"
          ></i-input>
        </FormItem>
        <FormItem
          label=""
          prop="machine_password"
          :rules="{
            required: true,
            min: 6,
            message: '6',
            trigger: 'blur'
          }"
        >
          <i-input
            v-model="machineFormValidate.machine_password"
            placeholder=""
          ></i-input>
        </FormItem>
        <FormItem
          label=""
          prop="machine_create_date"
          :rules="{ required: true }"
        >
          <DatePicker
            type="date"
            placeholder=""
            v-model="machineFormValidate.machine_create_date"
          ></DatePicker>
        </FormItem>
        <FormItem
          label=""
          prop="machine_end_date"
          :rules="{ required: true }"
        >
          <DatePicker
            type="date"
            placeholder=""
            v-model="machineFormValidate.machine_end_date"
          ></DatePicker>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "AddMachine",
  props: {
    showModal: Boolean
  },
  data() {
    return {
      show: false, // show 
      loading: true,
      machineFormValidate: {
        machine_no: "", // 
        machine_ip: "", // ip
        machine_password: "", // 
        machine_create_date: "", // 
        machine_end_date: "" // 
      }
    };
  },
  methods: {
    ...mapActions(["addMachine"]),
    asyncOK(name) {
      this.$refs[name].validate(valid => {
        if (!valid) {
          this.$Message.warning("");
        } else {
          this.addMachine({
            me: this,
            data: {
              machine_no: this.machineFormValidate.machine_no,
              machine_ip: this.machineFormValidate.machine_ip,
              machine_password: this.machineFormValidate.machine_password,
              machine_create_date: this.machineFormValidate.machine_create_date,
              machine_end_date: this.machineFormValidate.machine_end_date
            }
          });
        }
      });

      this.$refs[name].resetFields();
      this.show = false;
    }
  },
  watch: {
    showModal: function() {
      this.show = this.showModal;
    },
    show: function() {
      this.$emit("update:showModal", this.show);
    }
  }
};
</script>

<style lang="scss" scoped></style>
