<template>
  <div class="machine">
    <Table
      border
      :columns="machine_label"
      :data="getMachinesWithState($route.params.state)"
    >
      <template slot-scope="{ row }" slot="machine_no">
        <strong>{{ row.machine_no }}</strong>
      </template>
      <template slot-scope="{ row }" slot="action">
        <Button
        type="primary"
        size="small"
        style="margin-right: 5px"
        @click="edit(row)"
        ></Button>
        <Button type="error" size="small" @click="remove(row)"></Button>
      </template>
    </Table>
    <EditMachine
      :showModal.sync="showEditMachine"
      :machineDetail="activeMachine"
    ></EditMachine>
  </div>
</template>

<script>
import EditMachine from "@/components/EditMachine";
import util from "@/util/util"
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Machine",
  components: {
    EditMachine
  },
  data() {
    return {
      showEditMachine: false,
      activeMachine: {},
      machine_label: [
        {
          title: "",
          slot: "machine_no"
        },
        {
          title: "id",
          key: "account_id"
        },
        {
          title: "ip",
          key: "machine_ip"
        },
        {
          title: "",
          key: "machine_password"
        },
        {
          title: "",
          key: "machine_create_date",
          sortable: true
        },
        {
          title: "",
          key: "machine_end_date",
          sortable: true
        },
        {
          title: "",
          key: "machine_time_left",
          sortable: true
        },
        {
          title: "Action",
          slot: "action",
          width: 150,
          align: "center"
        }
      ]
    };
  },
  computed: {
    ...mapGetters(["getMachinesWithState"])
  },
  methods: {
    ...mapActions(['eidtMachine', 'delMachine']),
    edit(row) {
      this.showEditMachine = true;
      // js
      this.activeMachine =  util.deepCopy(row)
    },
    remove(item){
      if (item.account_id != '' && item.account_id != null) {
        this.$Notice.info({
          title: "",
          desc: ", id: "+item.account_id
        })
        return
      }
      this.delMachine({me: this, data: item})
    }
  }
};
</script>
<style lang="scss" scoped>
.machine {
  height: 100%;
}
</style>
