<template>
  <div class="edit-account">
    <Modal
      v-model="show"
      title=""
      :loading="loading"
      @on-ok="asyncOK('editAccountFormValidate')"
    >
      <Form
        ref="editAccountFormValidate"
        :model="editAccountFormValidate"
        :label-width="80"
      >
        <FormItem
          label=""
          prop="account_no"
          :rules="{
            required: true,
            message: ', a101',
            trigger: 'blur'
          }"
        >
          <i-input
            v-model="editAccountFormValidate.account_no"
            placeholder=""
            readonly
          ></i-input>
        </FormItem>
        <FormItem label="" prop="agent_name" :rules="{ required: true }">
          <Select
            v-model="editAccountFormValidate.agent_name"
            placeholder=""
          >
            <Option
              v-for="a in agents"
              :value="a.agent_name"
              :key="a.agent_name"
              >{{ a.agent_name }}</Option
            >
          </Select>
        </FormItem>
        <FormItem label="">
          <Select
            v-model="editAccountFormValidate.machine_no"
            placeholder=""
            clearable
            style="width: 250px"
          >
            <Option
              v-for="m in getEditMachines(accountDetail.machine_no)"
              :value="m.machine_no"
              :key="m.machine_no"
              >{{ m.machine_no }}</Option
            >
          </Select>
        </FormItem>
        <FormItem label="">
          <!--  -->
          <i-input
            v-model="editAccountFormValidate.create_date"
            readonly
          ></i-input>
        </FormItem>

        <FormItem label="">
          <DatePicker
            type="date"
            placeholder=""
            v-model="editAccountFormValidate.agent_date"
          ></DatePicker>
        </FormItem>

        <FormItem label="">
          <DatePicker
            type="date"
            placeholder=""
            v-model="editAccountFormValidate.end_date"
          ></DatePicker>
        </FormItem>

        <FormItem label="">
          <i-input
            v-model="editAccountFormValidate.time_left"
            placeholder=""
            readonly
          ></i-input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
import util from "@/util/util";
import { mapState, mapGetters, mapActions } from "vuex";
export default {
  name: "EditAccount",
  props: {
    showModal: Boolean,
    // *********  ******** //
    // jsprop
    // , copy,
    //  JSON.parse(JSON.stringif(obj)), accountDetail
    accountDetail: Object
  },
  data() {
    return {
      show: false, // show 
      loading: true,
      editAccountFormValidate: {}
    };
  },
  computed: {
    ...mapState(["agents"]),
    ...mapGetters(["getEditMachines"])
  },
  methods: {
    ...mapActions(["editAccount"]),
    asyncOK(name) {
      console.log(
        "===> editAccountFormValidate data: ",
        this.editAccountFormValidate
      );
      this.$refs[name].validate(valid => {
        if (!valid) {
          this.$Message.warning("");
        } else {
          this.editAccountFormValidate.agent_date = util.formatDate(
            this.editAccountFormValidate.agent_date
          );
          this.editAccountFormValidate.end_date = util.formatDate(
            this.editAccountFormValidate.end_date
          );
          this.editAccount({ me: this, data: this.editAccountFormValidate });
        }
      });
      this.show = false;
    }
  },
  watch: {
    showModal: function() {
      this.show = this.showModal;
    },
    show: function() {
      this.$emit("update:showModal", this.show);
    },
    accountDetail: function(newVal) {
      // accountdetail
      this.editAccountFormValidate = util.deepCopy(newVal);
    }
  }
};
</script>

<style lang="scss" scoped></style>
