<template>
  <div class="add-account">
    <Modal
      v-model="show"
      title=""
      :loading="loading"
      @on-ok="asyncOK('accountFormValidate')"
    >
      <Form
        ref="accountFormValidate"
        :model="accountFormValidate"
        :label-width="120"
      >
        <FormItem
          label=""
          prop="account_no"
          :rules="{
            required: true,
            message: ', a101',
            trigger: 'blur'
          }"
        >
          <i-input
            v-model="accountFormValidate.account_no"
            placeholder=""
          ></i-input>
        </FormItem>
        <FormItem label="" prop="agent_id" :rules="{ required: true }">
          <Select
            v-model="accountFormValidate.agent_id"
            placeholder=""
          >
            <Option v-for="a in agents" :value="a.id" :key="a.agent_name">{{
              a.agent_name
            }}</Option>
          </Select>
        </FormItem>
        <FormItem label="" prop="agent_date">
          <DatePicker
            type="date"
            placeholder=""
            v-model="accountFormValidate.agent_date"
          ></DatePicker>
        </FormItem>
        <FormItem label="" prop="end_date">
          <DatePicker
            type="date"
            placeholder=""
            v-model="accountFormValidate.end_date"
          ></DatePicker>
        </FormItem>
        <FormItem label="" prop="machine_id">
          <Select
            v-model="accountFormValidate.machine_id"
            placeholder=""
            clearable
          >
            <Option
              v-for="m in getMachinesWithState('useful')"
              :value="m.id"
              :key="m.machine_no"
              >{{ m.machine_no }}</Option
            >
          </Select>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from "vuex";
export default {
  name: "AddAccount",
  props: {
    showModal: Boolean
  },
  data() {
    return {
      show: false, // show 
      loading: true,
      accountFormValidate: {
        account_no: "", // 
        agent_id: "", // 
        agent_date: "", // 
        end_date: "", // 
        machine_id: "" // 
      }
    };
  },
  computed: {
    ...mapState(["agents"]),
    ...mapGetters(["getMachinesWithState"])
  },
  methods: {
    ...mapActions(["addAccount"]),
    asyncOK(name) {
      this.$refs[name].validate(valid => {
        if (!valid) {
          this.$Message.warning("");
        } else {
          this.addAccount({
            me: this,
            data: {
              account_no: this.accountFormValidate.account_no,
              agent_id: this.accountFormValidate.agent_id,
              agent_date: this.accountFormValidate.agent_date,
              end_date: this.accountFormValidate.end_date,
              machine_id: this.accountFormValidate.machine_id
            }
          });
        }
      });
      this.show = false;

      this.$refs[name].resetFields();
    },
  },
  watch: {
    showModal: function() {
      this.show = this.showModal;
    },
    show: function() {
      this.$emit("update:showModal", this.show);
    }
  }
};
</script>

<style lang="scss" scoped></style>
